@import url('https://fonts.googleapis.com/css?family=Rubik+Mono+One');
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro');

.logo {
  width: 100%;
  height: auto;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  margin-bottom: 50px;
}

.tudo {
  width: 100%;
  height: auto;
}

.municipioListar {
  font-family: 'Rubik Mono One', sans-serif;
  font-size: 2.5vw;
}

.cidadeRanking {
  font-family: 'Rubik Mono One', sans-serif;
  font-size: 2.5vw;
}

.opcao {
  font-size: 2vw;
}

.grafico {
  font-family: "Arial";
  color: blue;
}

.boxed {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 3vw;
  border: 1px solid black ;
  font-family: 'Rubik Mono One', sans-serif;
  -webkit-box-shadow: 0px 0px 17px 3px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 17px 3px rgba(0,0,0,0.75);
}

.prop {
  font-family: 'Source Code Pro', monospace;
}

.infoObras {
  margin: 15px;
}

.loader {
    margin: 0 auto;
    border: 16px solid #c67aff; /* Light grey */
    border-top: 16px solid #fbff21; /* Blue */
    border-radius: 80%;
    width: 100px;
    height: 100px;
    -webkit-animation: spin 0.8s linear infinite;
            animation: spin 0.8s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.titleObras{
  font-size: 2.3vw;
}

.footer {
  text-align: center;
}


.divImagens {
    margin-top: 35px;
}

.texto {
  margin-top: 25px;
  font-family: 'Rubik Mono One', sans-serif;
  font-size: 1.1em;
  color: yellow;
}

.equipe {
  font-family: 'Rubik Mono One', sans-serif;
  font-size: 1.1em;  text-align: center;
  color: yellow;
  margin-bottom: 20px;
}

.iconeMembro {
  border-radius: 50%;
  margin-top: 40px;
  margin-bottom: 10px;
  height: 165px;
}

.equipeTitle {
  margin-top: 20%;
  font-family: 'Rubik Mono One', sans-serif;
  font-size: 1.1em;  text-align: center;
  color: yellow;
}

.imgZinhas {
  margin-top: 30px;
  margin-bottom: 20px;
}


