@import url('https://fontlibrary.org/face/8bit-wonder');
@import url('https://fonts.googleapis.com/css?family=Space+Mono');

.mainView {
  background: -webkit-gradient(linear,left top, right bottom,from(#6e3f91), to(pink));
  background: -webkit-linear-gradient(top left,#6e3f91, pink);
  background: -o-linear-gradient(top left,#6e3f91, pink);
  background: linear-gradient(to bottom right,#6e3f91, pink);
}

.hvr-grow {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0);
            box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
}

.title {
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  color: #0fd1e7;
  margin: 0 auto;
  font-size: 10vw;
  text-shadow: 0px 0px 12px black;
  width: 100%;
  height: auto;
  font-family: '8BITWONDERNominal';
}

.setinha {
  margin-top: 50px;
  color: #0fd1e7;
  text-shadow: 0px 0px 10px black;
  margin-bottom: 50px;
  font-size: 4vw;
}


.viewObras {
  background: -webkit-gradient(linear, left bottom, right top, from(#fbff21) , to(#c67aff));
  background: -webkit-linear-gradient(bottom left, #fbff21 , #c67aff);
  background: -o-linear-gradient(bottom left, #fbff21 , #c67aff);
  background: linear-gradient(to top right, #fbff21 , #c67aff);
}

.viewPrimeiros {
  background: -webkit-gradient(linear, left top, right bottom, from(#fbff21) , to(#20fff3));
  background: -webkit-linear-gradient(top left, #fbff21 , #20fff3);
  background: -o-linear-gradient(top left, #fbff21 , #20fff3);
  background: linear-gradient(to bottom right, #fbff21 , #20fff3);
}

.viewUltimos{
  background: -webkit-gradient(linear, left bottom, right top, from(#ff2020) , to(#20fff3));
  background: -webkit-linear-gradient(bottom left, #ff2020 , #20fff3);
  background: -o-linear-gradient(bottom left, #ff2020 , #20fff3);
  background: linear-gradient(to top right, #ff2020 , #20fff3);
}

.grid {
  margin: 0 auto;
  text-align: center;
}

.hvr-grow:hover,
.hvr-grow:focus,
.hvr-grow:active {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.hvr-float {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0);
          box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
     transition-duration: 0.3s;
  -webkit-transition-property: transform;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  -o-transition-property: transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-timing-function: ease-out;
  -o-transition-timing-function: ease-out;
     transition-timing-function: ease-out;
}
.hvr-float:hover, .hvr-float:focus, .hvr-float:active {
  -webkit-transform: translateY(-8px);
  -ms-transform: translateY(-8px);
      transform: translateY(-8px);
}

.header {
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  border-color: transparent;
  text-align: center;
  margin-top: 50px;
  font-family: '8BITWONDERNominal';
}

.logo {
  width: 100%;
  height: auto;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  margin-bottom: 30px;
}

.go {
  height: 110px;
}

.subtitle {
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}

.subtitleText {
  color: #0fd1e7;
  text-shadow: 0px 0px 5px black;
  font-size: 0.5em;
}

.printDescription {
  margin-top: 15px;
  color: black;
  text-align: center;
  font-size: 1.8em;
  font-family: 'Space Mono', monospace;
  color: yellow;
  text-shadow: 0px 0px 11px black;
}

.carousel-inner > .item > img {
    margin: 0 auto;
    border-radius: 20px;
}

.carousel-control.left {
  background: transparent;
  color: #0fd1e7;
}

.carousel-control.right {
  background: transparent;
  color: #0fd1e7;
}

.carousel-indicators li {
  visibility: hidden;
}

.baixar {
  font-size: 5em;
  margin-top: 50px;
  color: #0fd1e7;
  text-shadow: 0px 0px 11px black;
}

.downloadRow {
  margin-top: 50px;
  margin-bottom: 50px;
  font-family: '8BITWONDERNominal';
  text-align: center;
  color: #0fd1e7;
}

.downloadtext {
  margin-top: 10px;
  text-shadow: 0px 0px 1px black;
  font-size: 1em;
  color: #0fd1e7;
}

.dadosText {
  margin-top: 10px;
  text-shadow: 0px 0px 1px black;
  font-size: 1em;
}

.footerView{
    background: -webkit-gradient(linear, left bottom, left top, from(#00232d) , to(#008472));
    background: -webkit-linear-gradient(bottom, #00232d , #008472);
    background: -o-linear-gradient(bottom, #00232d , #008472);
    background: linear-gradient(to top, #00232d , #008472);
}



